form{
    margin-top: 10px;
  }
input[type="text"] {
    width: 250px;
    height: 20px;
    outline: 2px solid lightgreen;
    border: none;

}
input[type="text"]:focus {
    width: 250px;
    height: 20px;
    outline: 2px solid lightgreen;
}
button {
    height: 26px;
    width: 60px;
    color: white;
    background-color: lightgreen;
    border: none;
    outline: none;
    cursor: pointer;
}